import { Injectable, ComponentFactoryResolver, ViewContainerRef, ComponentRef, ComponentFactory, Type } from '@angular/core';
import { Observable } from 'rxjs';

import { DialogComponent } from '../components/dialog/dialog.component';

@Injectable()
export class DynamicComponentsService {

  constructor(private componentFactoryResolver: ComponentFactoryResolver) { }
  public load<T extends DialogComponent>(component: Type<T>, vcr: ViewContainerRef): Observable<any> {
    return new Observable((subscriber) => this.loadDynamicComponent(component, vcr, subscriber));
  }
  private loadDynamicComponent<T extends DialogComponent>(component: Type<T>, vcr: ViewContainerRef, subscriber) {
    const factory: any = this.componentFactoryResolver.resolveComponentFactory(component);
    const componentRef = vcr.createComponent(factory);

    (<T>componentRef.instance).close = (data: any) => {
      componentRef.destroy();
      subscriber.next(data);
    };
  }
}
